<template>
    <div>
        <el-card v-loading="loadingQ">
            <!---->
            <div class="chat-header">
                <el-row>
                    <!---->
                    <el-col :span="20">
                        <el-pagination
                            small
                            @current-change="handleCurrentChange"
                            :page-size="num_each"
                            layout="prev, pager, next, jumper"
                            :total="record_count">
                        </el-pagination>
                    </el-col>
                    <!---->
                    <el-col :span="4">
                        <el-tooltip content="">
                            <el-button icon="el-icon-refresh" circle size="mini" @click="refresh"></el-button>
                        </el-tooltip>
                    </el-col>
                </el-row>
            </div>

            <!---->
            <div class="chat-record">
                <div v-for="(record, index) in chat_records" :key="index">
                    <!---->
                    <div v-if="displayTimeQ(record.date_time)" class="bubble-time">
                        {{ formatDateTime(record.date_time) }}
                    </div>
                    <!---->
                    <div v-if="record.type === 'text'">
                        <!---->
                        <div v-if="record.user_id === user.user_id">
                            <el-row>
                                <div class="bubble-me">
                                    {{ record.message }}
                                </div>
                            </el-row>
                        </div>
                        <!---->
                        <div v-else-if="record.course_status === 0">
                            {{ record.realname }} :
                            <el-row>
                                <div class="bubble-teacher">
                                    {{ record.message }}
                                </div>
                            </el-row>
                        </div>
                        <!---->
                        <div v-else>
                            {{ record.realname }} :
                            <el-row>
                                <div class="bubble-others">
                                    {{ record.message }}
                                </div>
                            </el-row>
                        </div>
                    </div>
                    <!---->
                    <div v-else-if="record.type === 'voice'">
                        <!---->
                        <div v-if="record.user_id === user.user_id">
                            <el-row>
                                <div class="bubble-me">
                                    <audio :src="record.path" controls></audio>
                                </div>
                            </el-row>
                        </div>
                        <!---->
                        <div v-else-if="record.course_status === 0">
                            {{ record.realname }} :
                            <el-row>
                                <div class="bubble-teacher">
                                    <audio :src="record.path" controls></audio>
                                </div>
                            </el-row>
                        </div>
                        <!---->
                        <div v-else>
                            {{ record.realname }} :
                            <el-row>
                                <div class="bubble-teacher">
                                    <audio :src="record.path" controls></audio>
                                </div>
                            </el-row>
                        </div>
                    </div>
                    <!---->
                    <div v-else-if="record.type === 'picture'">
                        <!---->
                        <div v-if="record.user_id === user.user_id">
                            <div style="text-align: right">
                                 : &nbsp; &nbsp;
                                <chat-picture :record="record"></chat-picture>
                            </div>
                        </div>
                        <!---->
                        <div v-else-if="record.course_status === 0">
                            <div style="text-align: left">
                                {{ record.realname }} :
                                <chat-picture :record="record"></chat-picture>
                            </div>
                        </div>
                        <!---->
                        <div v-else>
                            <div style="text-align: left">
                                {{ record.realname }} :
                                <chat-picture :record="record"></chat-picture>
                            </div>
                        </div>
                    </div>
                    <!---->
                    <div v-else>
                        {{ record.message }}
                    </div>
                </div>
            </div>
        </el-card>
    </div>
</template>

<script>
    import {parseFlow, parseList, formatDateTime} from './chat_records';
    import ChatPicture from "./ChatPicture"

    const MINUTES_SEPARATE = 5; // 
    var time_marker = undefined;
    export default {
        name: "ChatRecords",
        props: ['course_id', 'user'],
        data () {
            return {
                chat_records: [],
                loadingQ: true,
                record_count: 0, // total chat records
                num_each: 20, // display 20 messages on each page
            };
        },
        mounted () {
            this.refresh();
        },
        methods: {
            refresh () { // 
                this.updateRecordCount().
                     then((count) => {
                         this.handleCurrentChange(1);
                     });
            },
            updateRecordCount () { // 
                return new Promise((resolve, reject) => {
                    this.$http.
                         get('/api/live/get_chat_record_count', {params: {course_id: this.course_id,}}).
                         then((res) => {
                             if (res.body.status === 'FAILED.') {
                                 this.pushRecord({message: res.body.details});
                                 this.loadingQ = false;
                                 return;
                             } else {
                                 this.record_count = res.body.results;
                             }
                             resolve(this.record_count);
                         }).
                         catch((err) => {
                             reject(err);
                         });
                });
            },
            pushRecord (flow) { // 
                if (this.chat_records.length >= this.num_each) //  num_each  num_each 
                {
                    this.chat_records.pop();
                }
                time_marker = new Date();
                let record = parseFlow(flow); // 
                console.log('[new flow]', flow);
                this.chat_records = [record].concat(this.chat_records); // 
                this.record_count++;
            },
            clear () { // 
                this.chat_records = [];
                this.refresh();
            },
            displayTimeQ (date_time) {
                if ((time_marker.getTime() - date_time.getTime()) / 60000 > MINUTES_SEPARATE) {
                    time_marker = new Date(date_time.toString());
                    return true;
                } else {
                    return false;
                }
            },
            handleCurrentChange (page_ord) { //  page_ord 
                this.loadingQ = true;
                this.$http.
                     get('/api/live/get_chat_record', {
                         params: {
                             course_id: this.course_id,
                             start: (page_ord - 1) * this.num_each,
                             end: page_ord * this.num_each //  [start, end) 
                         }
                     }).
                     then((res) => {
                         this.loadingQ = false;
                         if (res.body.status === 'FAILED.') {
                             this.pushRecord({ message: res.body.details });
                         } else { // 
                             time_marker = new Date();
                             this.chat_records = parseList(res.body.results); // 
                             console.log('[record list]', res.body.results);
                         }
                     }).
                     catch((err) => {
                     });
            },
            formatDateTime: formatDateTime
        },
        components: {
            ChatPicture
        }
    };
</script>

<style scoped>
    .chat-record {
        height: 380px;
        font-size: 0.7em;
        overflow: auto;
    }

    .bubble-me, .bubble-others, .bubble-teacher {
        width: auto;
        max-width: 80%;
        height: 100%;
        margin: 10px;
        position: relative;
        font-size: 12px;
        line-height: 18px;
        padding: 5px 12px 5px 12px;
        box-sizing: border-box;
        border-radius: 6px;
    }

    .bubble-others::before, .bubble-me::after, .bubble-teacher::before {
        content: '';
        position: absolute;
        width: 8px;
        height: 8px;
        top: 10px;
        background: inherit;
        border: inherit;
        border-style: solid solid none none;
    }

    /**/
    .bubble-others {
        float: left;
        background-color: #fffdf8;
        border: 1px solid #d6c489;
    }

    .bubble-others::before, .bubble-teacher::before {
        left: -5px;
        transform: rotate(-135deg);
    }

    /**/
    .bubble-me {
        float: right;
        background-color: #e5ffea;
        border: 1px solid #a2c58e;
    }

    .bubble-me::after {
        right: -5px;
        transform: rotate(45deg);
    }

    /**/
    .bubble-teacher {
        float: left;
        background-color: #fff3f1;
        border: 2px solid #ae3832;
    }

    .bubble-time {
        width: 180px;
        height: 100%;
        margin: auto;
        position: relative;
        background-color: #c7c7c7;
        color: white;
        font-weight: bold;
        font-size: 12px;
        text-align: center;
        line-height: 18px;
        padding: 5px 12px 5px 12px;
        box-sizing: border-box;
        border-radius: 15px;
    }

    .chat-header {
        position: relative;
        margin: auto;
        padding: 5px 12px 5px 12px;
        border-bottom: #c6c6c6 1px dashed;
        background-color: #ffffff;
        top: -20px;
    }

</style>



