<template>
	<!---->
    <div>
        <el-button type="success" plain
                   v-if="!recordingQ"
                   icon="el-icon-phone-outline"
                   :disabled="blockQ"
                   @click="handleRecordStart">
            
        </el-button>
        <el-button type="danger" plain
                   v-if="recordingQ"
                   icon="el-icon-loading"
                   @click="handleRecordStop">
            
        </el-button>
    </div>
</template>

<script>
    import Recorder from 'recorder-js'; // 
	export default {
		name: "ChatRecorder",
        props: { blockQ: Boolean, course_id: String },
        data() {
		    return {
                recorder: undefined, // 
                recordingQ: false,   // 
            }
        },
        mounted() {
            // 
            const audioContext = new (window.AudioContext || window.webkitAudioContext)();
            this.recorder = new Recorder(audioContext);
            navigator.mediaDevices.getUserMedia({ audio: true }).
                      then(stream => this.recorder.init(stream)).
                      catch(err => console.log('[init recorder failed]', err));
        },
        methods: {
            handleRecordStart() {   // 
                this.recorder.start().
                     then(() => {
                         this.recordingQ = true;
                     });
            },
            handleRecordStop() {    // 
                this.recorder.stop().
                     then(({ blob, buffer }) => {
                         this.recordingQ = false;
                         this.sendVoice(blob);
                     });
            },
            sendVoice(blob) {   // 
                if (this.blockQ) return;
                let fd = new FormData();        //  FormData 
                fd.append('course_id', this.course_id);
                fd.append('blob', blob);
                let config = {
                    'headers': {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                this.$http.
                     post('/api/live/voice', fd, config).
                     // 
                     then(res => {
                         let msg = {
                             course_id: this.course_id,
                             type: 'voice',
                             message: '[]',
                             path: res.body.path     // 
                         };
                         this.$socket.emit('message', msg);
                         //  socket 
                     }).
                     catch(err => {
                         this.$message.error('');
                         console.log('[voice failed to upload]', err);
                     });
            },
        }
	}
</script>

<style scoped>

</style>



